'use strict';

/**
 * @function convertFromSeconds
 * @param  {Object} value [description]
 * @return {Boolean}       [description]
 */
var formatSeconds = function formatSeconds(value) {
    var second = 0;
    var minute = '';
    var hour = '';

    if (value < 60) {
        second = String(value) + 's';
    } else if (60 <= value && value < 3600) {
        var _sec = Math.floor(value % 60);
        var _min = Math.floor(value / 60);
        second = _sec < 10 ? '0' + _sec + 's' : _sec + 's';
        minute = _min < 10 ? '0' + _min + 'm' : _min + 'm';
    } else if (3600 <= value) {
        var minRaw = Math.floor(value / 60);
        var h = Math.floor(value / 3600);
        var min = Math.floor(minRaw % 60);
        var sec = Math.floor(value % 60);
        second = sec < 10 ? '0' + sec + 's' : sec + 's';
        minute = min < 10 ? '0' + min + 'm' : min + 'm';
        hour = h < 10 ? '0' + h + 'h' : h + 'h';
    };
    return hour + minute + second;
};

module.exports = formatSeconds;